.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.table-div {
  width: 50%;
  margin-left: 25%;
}

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tbody tr:nth-child(odd) {
  background-color: #dddddd;
}
h1 {
  text-align: center;
}
.button {
  width: 45%;

}
.actions {
  display: flex;
  justify-content: space-evenly;
}
.form-div {
  margin-top: 25px;
}
.form-group {
  margin: 10px 0 0 0;
}
.btn {
  margin-right: 3px;
}
.a {
  margin-top: 25px;
}